import classes from "./aboutUs.module.scss";

import video from "assets/video/arch_games.mp4";
import LoadingVideo from "assets/img/video_loading.png";

const AboutUsPage = () => {
  return (
    <div className={classes.wrapper}>
      <div className="rowGap-50">
        <div className="container">
          <video
            preload="auto"
            className={classes.video}
            autoPlay
            loop
            muted
            playsInline
            webkit-playsInline
            poster={LoadingVideo}
          >
            <source src={video} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </div>
        <div className="rowGap-30">
          <h4 className="title text-center">About Us</h4>
          <div className={classes.aboutUsDescription}>
            <div className="container">
              <p className="paragraph-black text-center">
                Arch Software is a forward-thinking technology solutions
                provider dedicated to crafting high-quality applications,
                immersive games, and powerful automation tools. Our team of
                experienced developers, designers, and engineers is committed to
                leveraging cutting-edge technologies and best practices to
                deliver solutions that propel businesses and individuals toward
                success.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutUsPage;
